/*-----------BANNER----------------*/
.banner {
    width: 100%;
    height: 700px;
    position: relative;
    
  
    &__elements {
    
      margin-top: 2em;
      margin-left: 81px;
      width: 450px;
      background-color: rgba(255, 255, 255, 0.151);
      padding: 50px;
      padding: 20px 50px 50px; 
      position: absolute;
     
      top: 50%;
      -webkit-transform: translateY(-50%);
       transform: translateY(-50%);
  
       a {
        
        font-weight: 600;
        color: white;
        
        padding: 18px 100px;
        background-color: #F64D33;
       }
    }
     &__background {
    
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover;
      /*adapta la imagen*/
    }
  }
  

  