.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill,230px);
    gap: 20px;
    justify-content: center;
    margin: 0 auto;
}

.grid-item {
 width: 230px;
 margin-bottom: 40px;

 cursor: zoom-in;
 background-color: rgb(119, 118, 118);
}

.grid-item:hover {
    filter:opacity(0.9);
}